<template>

    <div class="row ml-3 mr-0">
        <div class="col-12 pt-3">
            <slot name="header_1"></slot>

            <div class="mt-2 mc-hui def-shadow w-full br-12 mc-top-panel" :style="{ 'height': `${topH}vh` }">

                <slot name="content_1"></slot>
            </div>
        </div>
        <div class="col-12 pt-3 mt-3">
            <slot name="header_2"></slot>

            <div class="mt-2 mc-hui def-shadow w-full br-12 mc-bottom-panel" :style="{ 'height': `${bottomH}vh` }">

                
                <slot name="content_2"></slot>
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: '',
        props: {
            topH: {
                type: Number,
                default: 45
            },
            bottomH: {
                type: Number,
                default: 30
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/ready.sass"
</style>


