<template>
    <div class="container-fluid bg-hui h-100">
        <div class="h-full pos-rel" >

            <div class="pos-abs h-full w-full" :style="{ 'width': `${topH}px`, 'padding-top': `${topP}px` }">
                <div class="panel-top h-full" :class="{ 'pl-4': needPadding, 'pr-3': needPadding, }">
                    
                    <slot name="layout_top"></slot>

                </div>
            </div>

            <div class="bg-pink h-full" :style="{ 'padding-left': `${topH}px` }">
                <div class="panel-center" :class="{ 'pl-4': needPadding, 'pr-3': needPadding }">

                    <slot name="layout_center"></slot>
                    
                </div>
            </div>

        </div>
        
        <slot name="layout_extra"></slot>
    </div>
</template>

<script>
    export default {
        name: '',
        props: {
            topP: {
                type: Number,
                default: 0
            },
            topH: {
                type: Number,
                default: 180
            },
            needPadding: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

</style>


